import React, { PureComponent } from 'react'

import Collapsible from 'react-collapsible';

import { MdPlayArrow } from "react-icons/md";

import Box from '../components/Box'
import Flex from '../components/Flex'
import Link from '../components/Link'
import { responsive } from '../components/ThemeProvider/theme';

const isChinese = (text) => /.*[\u4e00-\u9fa5]+.*/.test(text)

export default class AllDatas extends PureComponent {

  state = {}

  handleOpen = () => {
    const { open } = this.state;
    this.setState({ open: !open })
  }

  render() {
    const { sortDatas, newData, n } = this.props
    const { open } = this.state
    return (
      <Box borderBottom="1px solid" pt="0.75em" pb="1em">
        <Collapsible
          open={open}
          handleTriggerClick={this.handleOpen}
          trigger={
            <Flex
              fontSize="1.5em"
              justifyContent="space-between"
            >
              <Flex>
                <Box transform={open && 'rotate(90deg)'}>
                  <MdPlayArrow />
                </Box>
                <Box>{n}</Box>
              </Flex>
              <Box>
                {newData[n][0].頁數}
              </Box>
            </Flex>
          }
        >
          {sortDatas.map((sortData, index) => (
            <Box px="0.5em" mt="1.5em" key={index}>
              {sortData.map(({作者, 年份, 內文標註, 文章標題, 期刊標示, 網址}, k) => (
                <Box pl={responsive(0, '5em')} mt="0.5em" fontSize="1.25em" position="relative" key={k}>
                  {!Boolean(k) && (
                    <Box position={responsive('relative', 'absolute')} pb={responsive('0.5em', 0)} top="0" left="0">
                      {內文標註 ? 內文標註 === 'R' ? '延伸閱讀' : '內文引用' : '參考資料'}
                    </Box>
                  )}
                  <Link href={網址}>
                    {內文標註.startsWith('*') ? 內文標註 : k + 1 + '.'} {作者}{年份 && `(${年份})`}{isChinese(作者) ? '。' : '. '}{文章標題 + (isChinese(文章標題) ?  '。' : '.')}{期刊標示}
                  </Link>
                </Box>
              ))}
            </Box>
          ))}
        </Collapsible>
      </Box>
    )
  }
}
