import React, { PureComponent } from 'react';
import { MdClose } from 'react-icons/md';

import Box from './Box';
import Input from './Input';

class InputWithClear extends PureComponent {
  state = {
    value: '',
  }

  handleChange = (e) => {
    this.props.onChange(e);
    this.setState({ value: e.target.value })
  }

  handleClear = () => {
    this.setState({ value: '' })
    this.props.onChange({
      target: { value: '' }
    });
  }

  render() {
    const { value } = this.state;
    return (
      <Box position="relative">
        <Input {...this.props} onChange={this.handleChange} value={value} />
        {/* <Box
          position="absolute"
          top="50%"
          right="0"
          transform="translate(-50%, -50%)"
          onClick={this.handleClear}
        >
          <MdClose size={24} />
        </Box> */}
      </Box>
    );
  }
}

InputWithClear.propTypes = {

};

InputWithClear.defaultProps = {
  onChange: () => {},
};

export default InputWithClear;
