import React, { PureComponent } from 'react'
import groupBy from 'lodash/groupBy';
import sortBy from 'lodash/sortBy';

import Container from '../components/Container'
import Box from '../components/Box'
import Flex from '../components/Flex'
import Text from '../components/Text'
import Input from '../components/Input'
import InputWithClear from '../components/InputWithClear'
import Button from '../components/Button'
import { responsive } from '../components/ThemeProvider/theme';

import withData from '../services/api/withData'

import Layout from './Layout'
import AllDatas from './AllDatas';

class HomePage extends PureComponent {

  state = {
    search: '',
    GOB: '',
    page: ''
  }

  setGOB = (GOB, result) => {
    const { page } = this.state;
    if (GOB === this.state.GOB) {
      return this.setState({ GOB: '', result: page })
    }
    if (result && GOB !== this.state.GOB) {
      return this.setState({ GOB, result: GOB + page })
    }
    this.setState({ GOB })
  }

  handleSearch = (e) => {
    this.setState({ search: e.target.value });
  }

  setPage = (e) => {
    const { GOB } = this.state;
    this.setState({ result: GOB + e.target.value, page: e.target.value});
  }

  render() {
    const { data } = this.props;
    const { GOB, search, result } = this.state;
    const filterDates = result || GOB ? GOB.includes('G') ? data.filter(d => d.頁數.includes(result || GOB)).filter(d => d.主題.includes(search)) : data.filter(d => d.頁數.includes(result || GOB)).filter(d => d.主題.includes(search)) : data.filter(d => d.主題.includes(search))
    const newData = groupBy(filterDates, '主題')
    const names = Object.keys(newData)
    console.log(data)
    return (
      <Layout>
        <Container py={responsive('1em', '2em')}>
          <Box borderBottom="1px solid" pb="2em" px="1em">
            <Flex alignItems="center">
              <Text fontSize={responsive('1.25em','1.75em')} mr="2em">頁面尋找</Text>
              <Flex flex={1}>
                <Button active={GOB === 'G'} onClick={() => this.setGOB('G', result)}>G</Button>
                <Button
                  active={GOB === 'B'}
                  onClick={() => this.setGOB('B', result)}
                  border="none"
                  borderRight="1px solid"
                  borderTop="1px solid"
                  borderBottom="1px solid"
                >
                  B
                </Button>
                <Input
                  ml={responsive('0.5em', '1em')}
                  width="3em"
                  type="number"
                  onChange={this.setPage}
                  textAlign="center"
                  placeholder="22"
                  step="2"
                  min="10"
                  max="138"
                />
              </Flex>
            </Flex>
            <Flex alignItems="center" mt="1em">
              <Text fontSize={responsive('1.25em','1.75em')} mr="1em">關鍵字搜尋</Text>
              <Box flex={1}>
                <InputWithClear onChange={this.handleSearch} />
              </Box>
            </Flex>
          </Box>
          <Box px="1em">
            <Text fontSize="1.75em" mt="1em">{GOB}頁碼</Text>
            <Box my="0.5em">點擊各參考資料、延伸閱讀即可連結至原始網頁頁面</Box>
            {names.map((n, i) => {
              const sortAllDatas = [
                sortBy(newData[n].filter(d => d.內文標註.startsWith('*')), '內文標註'),
                newData[n].filter(d => d.內文標註 === ""),
                newData[n].filter(d => d.內文標註 === 'R')
              ]
              return (
                <AllDatas GOB={GOB} sortDatas={sortAllDatas} newData={newData} n={n} key={i} />
            )})}
          </Box>
        </Container>
      </Layout>
    )
  }
}

export default withData('data')(HomePage)
